    /**
     * VerifyPoints
     * @description 点选
     * */
    import {aesEncrypt} from "./../utils/ase.js"
	import {myRequest} from "../utils/request.js"
    export default {
        name: 'VerifyPoints',
        props: {
            //弹出式pop，固定fixed
            mode: {
                type: String,
                default: 'fixed'
            },
            captchaType:{
                type:String,
            },
            //间隔
            vSpace: {
                type: Number,
                default: 5
            },
            imgSize: {
                type: Object,
                default() {
                    return {
                        width: '310px',
                        height: '155px'
                    }
                }
            },
            barSize: {
                type: Object,
                default() {
                    return {
                        width: '310px',
                        height: '40px'
                    }
                }
            }
        },
        data() {
            return {
                secretKey:'',        //后端返回的加密秘钥 字段
                checkNum:3,                 //
                fontPos: [],                // 选中的坐标信息
                checkPosArr: [],            //用户点击的坐标
                num: 1,                     //点击的记数
                pointBackImgBase:'',        //后端获取到的背景图片
                poinTextList:[],            //后端返回的点击字体顺序
                backToken:'',               //后端返回的token值
                imgRand: 0,                 //随机的背景图片
                setSize: {
                    imgHeight: 0,
                    imgWidth: 0,
                    barHeight: 0,
                    barWidth: 0
                },
                showImage: true,
                tempPoints: [],
                text: '',
                barAreaColor: '#fff',
                barAreaBorderColor: "#fff",
                showRefresh: true,
                bindingClick: true,
                imgLeft:'' ,
                imgTop:'',
            }
        },
        methods: {
            init() {
                //加载页面
                this.fontPos.splice(0, this.fontPos.length)
                this.checkPosArr.splice(0, this.checkPosArr.length)
                this.num = 1
                this.$nextTick(() => {
                    this.refresh();
                    this.$parent.$emit('ready', this)
                })
            },
            canvasClick(e) {
                const query = uni.createSelectorQuery().in(this);
                query.select('#image').boundingClientRect(data => {
                    this.imgLeft =Math.ceil(data.left)
                    this.imgTop =Math.ceil(data.top) 
                    this.checkPosArr.push(this.getMousePos(this.$refs.canvas, e));
                    if (this.num == this.checkNum) {
                        this.num = this.createPoint(this.getMousePos(this.$refs.canvas, e));
                        //按比例转换坐标值
                        this.checkPosArr = this.pointTransfrom(this.checkPosArr,this.imgSize);
                        //等创建坐标执行完
                        setTimeout(() => {
                            //发送后端请求
                            var captchaVerification =this.secretKey? aesEncrypt(this.backToken+'---'+JSON.stringify(this.checkPosArr),this.secretKey):this.backToken+'---'+JSON.stringify(this.checkPosArr)
                            let data = {
                                captchaType:this.captchaType,
                                "pointJson":this.secretKey? aesEncrypt(JSON.stringify(this.checkPosArr),this.secretKey):JSON.stringify(this.checkPosArr),
                                "token":this.backToken
                            }
                            myRequest({
                                url: `/captcha/check`, 
                                data,
                                method:"POST",
                            }).then(result => {
                                    let res = result.data
                                    if (res.repCode == "0000") {
                                        this.barAreaColor = '#4cae4c'
                                        this.barAreaBorderColor = '#5cb85c'
                                        this.text = '验证成功'
                                        this.bindingClick = false
										setTimeout(()=>{
											if (this.mode=='pop') {
												this.$parent.clickShow = false;
											}
											this.refresh();
										},1500)
                                        this.$parent.$emit('success', {captchaVerification})
                                    }else{
                                        this.$parent.$emit('error', this)
                                        this.barAreaColor = '#d9534f'
                                        this.barAreaBorderColor = '#d9534f'
                                        this.text = '验证失败'
                                        setTimeout(() => {
                                            this.refresh();
                                        }, 700);
                                    }
                                })    
                        }, 400);
                    }
                    if (this.num < this.checkNum) {
                        this.num = this.createPoint(this.getMousePos(this.$refs.canvas, e));
                    }
                }).exec();
            },
            //获取坐标
            getMousePos: function (obj, e) {
                let position = {
                    x:Math.ceil(e.detail.x)-this.imgLeft,
                    y:Math.ceil(e.detail.y)-this.imgTop,
                }
                return position
            },
            //创建坐标点
            createPoint: function (pos) {
                this.tempPoints.push(Object.assign({}, pos))
                return ++this.num;
            },
            refresh: function () {
                this.tempPoints.splice(0, this.tempPoints.length)
                this.barAreaColor = '#000'
                this.barAreaBorderColor = '#ddd'
                this.bindingClick = true

                this.fontPos.splice(0, this.fontPos.length)
                this.checkPosArr.splice(0, this.checkPosArr.length)
                this.num = 1

                this.getPictrue();

                // this.text = '验证失败'
                this.showRefresh = true
            },
            // 请求背景图片和验证图片
            getPictrue(){
                let data = {
                    captchaType:this.captchaType
                }
                myRequest({
                    url: "/captcha/get", //仅为示例，并非真实接口地址。
                    data,
                    method:"POST",
                }).then((result) => {
                        let res = result.data
                        if (res.repCode == "0000") {
                            this.pointBackImgBase = res.repData.originalImageBase64
                            this.backToken = res.repData.token
                            this.secretKey = res.repData.secretKey
                            this.poinTextList = res.repData.wordList
                            this.text = '请依次点击【' + this.poinTextList.join(",") + '】'
                        }
                    })
            },
            //坐标转换函数
            pointTransfrom(pointArr,imgSize){
                var newPointArr = pointArr.map(p=>{
                    let x = Math.round(310 * p.x/parseInt(imgSize.width)) 
                    let y =Math.round(155 * p.y/parseInt(imgSize.height)) 
                    return {x,y}
                })
                // console.log(newPointArr,"newPointArr");
                return newPointArr
            },
        },
        watch: {
            // type变化则全面刷新
            type: {
                immediate: true,
                handler() {
                    this.init()
                }
            }
        },
    }
